import { Component, Element, Event, EventEmitter, Host, Method, Prop, h } from '@stencil/core';
import PerfectScrollbar from 'perfect-scrollbar';

@Component({
  tag: 'storm-split-view',
  styleUrl: 'storm-split-view.css',
  shadow: false,
})
export class StormSplitView {
  private _splitBarMove: boolean = false;
  private _splitBarRef: HTMLDivElement;
  private _stormContainerRef: HTMLDivElement;
  private _secondPaneRef: HTMLDivElement;

  @Element() host: HTMLElement;
  @Prop() splitPercent: number = 50;
  @Prop() fixedSize?: string;
  @Prop() fixedPane?: 'first' | 'second';
  @Prop() direction: 'horizontal' | 'vertical' = 'vertical';
  @Prop() firstPaneVisible: boolean = true;
  @Prop() secondPaneVisible: boolean = true;
  @Prop() firstPaneName: string = 'first-pane';
  @Prop() secondPaneName: string = 'second-pane';

  @Event() innerScroll: EventEmitter<number>;

  @Method()
  async viewToggle(token: 'first' | 'second', force: boolean) {
    this._stormContainerRef.classList.toggle('storm-' + token + '-pane-d-none', !force);
  }

  renderPane(token: string) {
    const classes = {};
    classes['storm-' + token + '-pane'] = true;
    return (
      <div
        ref={elm => {
          if (token === 'second') this._secondPaneRef = elm;
        }}
        class={classes}
      >
        <div class={{ 'storm-pane': true }}>
          <slot name={token === 'first' ? this.firstPaneName : this.secondPaneName}></slot>
        </div>
      </div>
    );
  }

  render() {
    const styles = {};
    if (!this.fixedSize || !this.fixedPane) styles['--split-percent'] = this.splitPercent + '%';
    else styles['--fixed-size'] = this.fixedSize;
    return (
      <Host style={styles}>
        <div
          ref={elm => (this._stormContainerRef = elm)}
          class={{
            'storm-container': true,
            'storm-first-pane-d-none': !this.firstPaneVisible,
            'storm-second-pane-d-none': !this.secondPaneVisible,
            'storm-split-percent': !this.fixedSize || !this.fixedPane,
            'storm-fixed-first-pane': this.fixedPane === 'first',
            'storm-fixed-second-pane': this.fixedPane === 'second',
            'storm-horizontal-split': this.direction === 'horizontal',
            'storm-vertical-split': this.direction === 'vertical',
          }}
        >
          {this.renderPane('first')}
          {this.renderPane('second')}
          <div
            ref={elm => {
              this._splitBarRef = elm;
            }}
            class={{ 'split-bar': true }}
          ></div>
        </div>
      </Host>
    );
  }

  componentDidLoad() {
    this._splitBarRef.addEventListener('mousedown', () => {
      this._splitBarRef.classList.add('active');
      this._splitBarMove = true;
      const staticBackdrop: HTMLDivElement = document.createElement('div');
      staticBackdrop.classList.add('storm-' + this.direction + '-static-backdrop');
      staticBackdrop.addEventListener('mousemove', (e: MouseEvent) => {
        if (this._splitBarMove) {
          if (this.direction === 'vertical') this._splitBarRef.style.setProperty('left', e.offsetX + 'px');
          else this._splitBarRef.style.setProperty('top', e.offsetY + 'px');
        }
      });
      staticBackdrop.addEventListener('mouseup', e => {
        this._splitBarRef.classList.remove('active');
        if (this._splitBarMove) {
          let percent = 0;
          if (this.direction === 'vertical') percent = (e.offsetX / staticBackdrop.offsetWidth) * 100;
          else percent = (e.offsetY / staticBackdrop.offsetHeight) * 100;
          if (percent < 10) percent = 10;
          if (percent > 90) percent = 90;
          this.host.style.setProperty('--split-percent', percent + '%');
          this._splitBarMove = false;
          if (this.direction === 'vertical') this._splitBarRef.style.removeProperty('left');
          else this._splitBarRef.style.removeProperty('top');
          this.host.removeChild(staticBackdrop);
        }
      });
      this.host.appendChild(staticBackdrop);
    });

    const container = this._secondPaneRef.firstElementChild;
    if (container) {
      new PerfectScrollbar(container);
      const innerScroll = this.innerScroll;
      let lastKnownScrollPosition = 0;
      let ticking = false;
      container.addEventListener('scroll', (e: Event) => {
        lastKnownScrollPosition = container.scrollTop;
        if (!ticking) {
          window.requestAnimationFrame(function () {
            innerScroll.emit(lastKnownScrollPosition);
            ticking = false;
          });
          ticking = true;
        }
        e.stopPropagation();
      });
    }
  }
}
