@import '../../../node_modules/perfect-scrollbar/css/perfect-scrollbar.css';

:host {
  display: block;
}

.storm-container,
.storm-vertical-static-backdrop,
.storm-horizontal-static-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.storm-first-pane-d-none>.first-second-pane,
.storm-second-pane-d-none>.storm-second-pane {
  display: none !important;
}

.storm-pane {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  /* overflow: hidden; */
}

.storm-split-percent:has(.storm-first-pane-AD-none, .storm-second-pane-d-none)>.split-bar {
  display: none;
}

.storm-split-percent:not(.storm-first-pane-d-none):not(.storm-second-pane-d-none)>.split-bar {
  -webkit-background-clip: padding-box;
  /* for Safari */
  background-clip: padding-box;
  /* for IE9+, Firefox 4+, Opera, Chrome */
  position: absolute;
  z-index: 9998;
}

.split-bar.active,
.split-bar:hover {
  background-color: #e91e63;
}

.storm-vertical-static-backdrop,
.storm-horizontal-static-backdrop {
  z-index: 9999;
}

.storm-vertical-split>.storm-first-pane,
.storm-vertical-split>.storm-second-pane {
  position: absolute;
  top: 0;
  bottom: 0;
}

.storm-vertical-split>.storm-first-pane {
  left: 0;
}

.storm-vertical-split>.storm-second-pane {
  right: 0;
}

.storm-vertical-split.storm-split-percent>.storm-first-pane {
  right: auto;
  width: var(--split-percent);
}

.storm-vertical-split.storm-split-percent>.storm-second-pane {
  left: var(--split-percent);
  width: auto;
}

.storm-vertical-split.storm-fixed-first-pane>.storm-first-pane {
  right: auto;
  width: var(--fixed-size);
}

.storm-vertical-split.storm-fixed-first-pane>.storm-second-pane {
  left: var(--fixed-size);
  width: auto;
}

.storm-vertical-split.storm-fixed-second-pane>.storm-first-pane {
  right: var(--fixed-size);
  width: auto;
}

.storm-vertical-split.storm-fixed-second-pane>.storm-second-pane {
  left: auto;
  width: var(--fixed-size);
}

.storm-vertical-split.storm-first-pane-d-none>.storm-second-pane,
.storm-vertical-split.storm-second-pane-d-none>.storm-first-pane {
  left: 0 !important;
  right: 0 !important;
  width: auto !important;
}

.storm-vertical-split.storm-split-percent:not(.storm-first-pane-d-none):not(.storm-second-pane-d-none):not(.storm-fixed-first-pane):not(.storm-fixed-second-pane)>.storm-first-pane>.storm-pane {
  right: 2px !important;
}

.storm-vertical-split.storm-split-percent:not(.storm-first-pane-d-none):not(.storm-second-pane-d-none):not(.storm-fixed-first-pane):not(.storm-fixed-second-pane)>.storm-second-pane>.storm-pane {
  left: 2px !important;
}

.storm-vertical-split.storm-split-percent:not(.storm-first-pane-d-none):not(.storm-second-pane-d-none)>.split-bar {
  border-left: 1px solid rgba(0, 0, 0, 0);
  border-right: 1px solid rgba(0, 0, 0, 0);
  top: 0;
  bottom: 0;
  left: var(--split-percent);
  margin-left: -2px;
  width: 4px;
}

.storm-vertical-static-backdrop,
.storm-vertical-split .split-bar:hover {
  cursor: col-resize;
}

.storm-horizontal-split>.storm-first-pane,
.storm-horizontal-split>.storm-second-pane {
  position: absolute;
  left: 0;
  right: 0;
}

.storm-horizontal-split>.storm-first-pane {
  top: 0;
}

.storm-horizontal-split>.storm-second-pane {
  bottom: 0;
}

.storm-horizontal-split.storm-split-percent>.storm-first-pane {
  bottom: auto;
  height: var(--split-percent);
}

.storm-horizontal-split.storm-split-percent>.storm-second-pane {
  top: var(--split-percent);
  height: auto;
}

.storm-horizontal-split.storm-fixed-first-pane>.storm-first-pane {
  bottom: auto;
  height: var(--fixed-size);
}

.storm-horizontal-split.storm-fixed-first-pane>.storm-second-pane {
  top: var(--fixed-size);
  height: auto;
}

.storm-horizontal-split.storm-fixed-second-pane>.storm-first-pane {
  bottom: var(--fixed-size);
  height: auto;
}

.storm-horizontal-split.storm-fixed-second-pane>.storm-second-pane {
  top: auto;
  height: var(--fixed-size);
}

.storm-horizontal-split.storm-first-pane-d-none>.storm-second-pane,
.storm-horizontal-split.storm-second-pane-d-none>.storm-first-pane {
  top: 0 !important;
  bottom: 0 !important;
  height: auto !important;
}

.storm-horizontal-split.storm-split-percent:not(.storm-first-pane-d-none):not(.storm-second-pane-d-none):not(.storm-fixed-first-pane):not(.storm-fixed-second-pane)>.storm-first-pane>.storm-pane {
  bottom: 2px !important;
}

.storm-horizontal-split.storm-split-percent:not(.storm-first-pane-d-none):not(.storm-second-pane-d-none):not(.storm-fixed-first-pane):not(.storm-fixed-second-pane)>.storm-second-pane>.storm-pane {
  top: 2px !important;
}

.storm-horizontal-split.storm-split-percent:not(.storm-first-pane-d-none):not(.storm-second-pane-d-none)>.split-bar {
  border-top: 1px solid rgba(0, 0, 0, 0);
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  left: 0;
  right: 0;
  top: var(--split-percent);
  margin-top: -2px;
  height: 4px;
}

.storm-horizontal-static-backdrop,
.storm-horizontal-split>.split-bar:hover {
  cursor: row-resize;
}