import { Component, Element, Event, EventEmitter, h, Host, Prop, State } from '@stencil/core';
import { FnHr, FnIcon } from '@utils/functional/functional';
import { mergeClasses } from '@utils/utils';
import PerfectScrollbar from 'perfect-scrollbar';

@Component({
  tag: 'storm-card',
  styleUrl: 'storm-card.css',
  shadow: false,
})
export class StormCard {
  private _cardBodyRef: HTMLElement;

  @State() _slotCardHeader: boolean = false;
  @State() _slotTitle: boolean = false;
  @State() _slotHeader: boolean = false;
  @State() _slotFooter: boolean = false;
  @State() _slotBody: boolean = false;

  private _perfectScrollbar: PerfectScrollbar;
  private _scrollListener: (ev: Event) => void = null;

  @Element() host: HTMLElement;
  @Prop() class: string;
  @Prop() icon?: string;
  @Prop() headline?: string;
  @Prop() bodyClass: string;
  @Prop({ reflect: true }) scrollable: boolean = false;
  @Prop() cardStyle: 'info' | 'dark' | 'primary' | 'success' | 'warning' | 'danger' = 'info';

  @Event() innerScroll: EventEmitter<number>;

  private _observer = new MutationObserver(() => {
    this.checkSlots();
  });

  private readonly checkSlots = () => {
    const slots = { cardHeader: this._slotCardHeader, title: this._slotTitle, header: this._slotHeader, footer: this._slotFooter, body: this._slotBody };
    const children = this.host.children;
    const targetSlots = new Set(['card-header', 'title', 'header', 'body', 'footer']);
    let foundCount = 0;

    for (let i = 0; i < children.length; i++) {
      const child = children[i];
      const slot = child.getAttribute('slot');

      if (slot && targetSlots.has(slot)) {
        switch (slot) {
          case 'card-header':
            if (!slots.cardHeader) {
              slots.cardHeader = true;
              foundCount++;
            }
            break;
          case 'title':
            if (!slots.title) {
              slots.title = true;
              foundCount++;
            }
            break;
          case 'header':
            if (!slots.header) {
              slots.header = true;
              foundCount++;
            }
            break;
          case 'footer':
            if (!slots.footer) {
              slots.footer = true;
              foundCount++;
            }
            break;
        }
      } else if (!slot && child.nodeType === Node.ELEMENT_NODE && !slots.body) {
        slots.body = true;
        foundCount++;
      }

      if (foundCount === 5) break;
    }

    this._slotCardHeader = slots.cardHeader;
    this._slotTitle = slots.title;
    this._slotHeader = slots.header;
    this._slotFooter = slots.footer;
    this._slotBody = slots.body;
  };

  componentWillRender() {
    this._observer.observe(this.host, {
      childList: true, // 偵測子節點新增或刪除
      subtree: false, // 若要監聽所有後代節點，設為 true
    });
  }

  componentDidRender() {
    if (!this._cardBodyRef) return;

    this.scrollable ? this.initScrollbar() : this.disposeScrollbar();
  }

  private _cardClassesCache: Record<string, boolean> | null = null;
  private _lastCardProps: string = '';

  private buildCardClasses() {
    const currentProps = `${this.class || ''}-${this.icon || ''}`;
    if (this._cardClassesCache && this._lastCardProps === currentProps) {
      return this._cardClassesCache;
    }

    const baseClasses = { card: true };
    const classes: Record<string, boolean> = this.class ? mergeClasses(baseClasses, this.class) : baseClasses;

    const hasMarginTop = Object.keys(classes).some(key => /^mt-[0-5]|mt-auto$/.test(key));

    if (!hasMarginTop) {
      classes[this.icon ? 'mt-4' : 'mt-3'] = true;
    }

    this._cardClassesCache = classes;
    this._lastCardProps = currentProps;
    return classes;
  }

  private _bodyClassesCache: Record<string, boolean> | null = null;
  private _lastBodyProps: string = '';

  private buildBodyClasses() {
    const currentProps = `${this.bodyClass || ''}-${this.scrollable}`;
    if (this._bodyClassesCache && this._lastBodyProps === currentProps) {
      return this._bodyClassesCache;
    }

    const baseClasses = {
      'card-body': true,
      'py-2': true,
      'overflow-auto': this.scrollable,
    };
    const classes = this.bodyClass ? mergeClasses(baseClasses, this.bodyClass) : baseClasses;

    this._bodyClassesCache = classes;
    this._lastBodyProps = currentProps;
    return classes;
  }

  private initScrollbar() {
    this.disposeScrollbar();

    this._perfectScrollbar = new PerfectScrollbar(this._cardBodyRef);

    let lastKnownScrollPosition = 0;
    let frameId: number | null = null;

    this._scrollListener = (ev: Event) => {
      ev.stopPropagation();
      const currentScrollPosition = this._cardBodyRef.scrollTop;

      if (Math.abs(currentScrollPosition - lastKnownScrollPosition) < 1) return;

      lastKnownScrollPosition = currentScrollPosition;

      if (frameId) {
        cancelAnimationFrame(frameId);
      }

      frameId = requestAnimationFrame(() => {
        this.innerScroll.emit(lastKnownScrollPosition);
        frameId = null;
      });
    };

    this._cardBodyRef?.addEventListener('scroll', this._scrollListener, { passive: true });
  }

  private disposeScrollbar() {
    if (this._scrollListener) {
      this._cardBodyRef?.removeEventListener('scroll', this._scrollListener);
      this._scrollListener = null;
    }

    if (this._perfectScrollbar) {
      this._perfectScrollbar.destroy();
      this._perfectScrollbar = null;
    }
  }

  disconnectedCallback() {
    this.disposeScrollbar();
    this._cardClassesCache = null;
    this._bodyClassesCache = null;
    this._iconCache = null;
    this._titleCache = null;

    this._observer.disconnect();
  }

  private _iconCache: any = null;
  private _lastIconProps = '';

  renderIcon() {
    if (!this.icon) return;

    const currentProps = `${this.icon}-${this.cardStyle}`;
    if (this._iconCache && this._lastIconProps === currentProps) {
      return this._iconCache;
    }

    const classes: Record<string, boolean> = {
      icon: true,
      'icon-lg': true,
      'icon-shape': true,
      'text-center': true,
      'border-radius-xl': true,
      'mt-n4': true,
      'me-3': true,
      'mb-2': true,
    };
    classes['bg-gradient-' + this.cardStyle] = true;
    classes['shadow-' + this.cardStyle] = true;

    this._iconCache = (
      <div class={classes}>
        <FnIcon class="opacity-10">{this.icon}</FnIcon>
      </div>
    );
    this._lastIconProps = currentProps;
    return this._iconCache;
  }

  private _titleCache: any = null;
  private _lastHeadline = '';

  renderCardTitle() {
    if (!this.headline) return;

    if (this._titleCache && this._lastHeadline === this.headline) {
      return this._titleCache;
    }

    this._titleCache = <h5 class="mt-3 me-auto">{this.headline}</h5>;
    this._lastHeadline = this.headline;
    return this._titleCache;
  }

  renderTitleSlot() {
    if (!this._slotTitle) return;
    return <slot name="title" />;
  }

  renderHeader() {
    if (this._slotCardHeader) return <slot name="card-header" />;
    if (!this._slotHeader && !this._slotTitle && !this.icon && !this.headline) return;
    return (
      <div
        class={{
          'card-header': true,
          'py-2': true,
        }}
      >
        <div class="title-header d-flex">
          {this.renderIcon()}
          {this.renderCardTitle()}
          {this.renderTitleSlot()}
        </div>
        {this.renderHeaderSlot()}
      </div>
    );
  }

  renderHeaderSlot() {
    if (!this._slotHeader) return;
    return <slot name="header" />;
  }

  renderBody() {
    if (!this._slotBody) return;
    return [
      <FnHr />,
      <div class={this.buildBodyClasses()} ref={elm => (this._cardBodyRef = elm)}>
        <slot name="body" />
      </div>,
    ];
  }

  renderFooterSlot() {
    if (!this._slotFooter) return;
    return [
      <FnHr />,
      <div class="card-footer py-2">
        <slot name="footer" />
      </div>,
    ];
  }

  render() {
    return (
      <Host>
        <div class={this.buildCardClasses()}>
          {this.renderHeader()}
          {this.renderBody()}
          {this.renderFooterSlot()}
        </div>
      </Host>
    );
  }
}
