import { Component, Element, Event, EventEmitter, h, Host, Listen, Method, Prop, Watch } from '@stencil/core';
import { FnIcon, FnSearchInput } from '@utils/functional/functional';
import type { ListItemInterface } from '@utils/interface';
import { appendHighlight, arrayEqual, calculateObjectValue, generateUid, getSelectedItemIds, mergeClasses } from '@utils/utils';

@Component({
  tag: 'storm-list-group',
  styleUrl: 'storm-list-group.scss',
  shadow: false,
})
export class StormListGroup {
  private _itemRefs: Record<string, HTMLElement>;
  private _searchRef: HTMLStormInputGroupElement;

  @Element() host: HTMLElement;

  @Prop({ mutable: true }) dataSet: ListItemInterface[];
  @Prop() headline?: string;
  @Prop({ mutable: true }) searchText?: string;
  @Prop() searchable: boolean = false;
  @Prop() selection: 'single' | 'multiple' | 'none' = 'single';
  @Prop() nullable: boolean = false;
  @Prop({ mutable: true }) value?: string | string[];
  @Prop() class: Record<string, boolean> | string;
  @Prop() itemClass: Record<string, boolean> | string;

  @Event() valueChanged: EventEmitter<string | string[]>;
  @Event() itemClick: EventEmitter<ListItemInterface>;
  @Event() selectChange: EventEmitter<ListItemInterface>;
  @Event() searchChanged: EventEmitter<string>;

  @Watch('value')
  valueWatcher(newValue: string | string[], oldValue: string | string[]) {
    if (arrayEqual(newValue, oldValue)) return;

    this.setSelectedItems();

    this.valueChanged.emit(newValue);
  }

  @Watch('dataSet')
  dataSetWatcher() {
    this.initDataSet();
    this.setSelectedItems();
  }

  @Listen('itemClick')
  itemClickHandler(event: CustomEvent<ListItemInterface>) {
    if (this.selection === 'none') return;
    const item = event.detail;
    item.selected ? this.unselect(item) : this.select(item);
  }

  @Listen('searchChanged')
  searchChangedHandler(ev: CustomEvent<string>) {
    this.searchText = ev.detail;
  }

  @Method()
  async setSearchFocus() {
    if (this.searchable && this._searchRef) {
      requestAnimationFrame(async () => {
        await this._searchRef.setFocus();
      });
    }
  }

  handleSearchChanged(ev: CustomEvent<string>) {
    ev.stopPropagation();
    const value = ev.detail?.trim();
    if (this.searchText === value) return;
    this.searchChanged.emit(value);
  }

  select(item: ListItemInterface) {
    if (item.selected) return;

    switch (this.selection) {
      case 'single':
        this.value = item.id;
        break;
      case 'multiple': {
        const currentValue = this.getCurrentValueAsArray();
        if (!currentValue.includes(item.id)) {
          this.value = [...currentValue, item.id];
        }
        break;
      }
      default:
        console.warn(`Unknown selection mode: ${this.selection}`);
    }
    this.selectChange.emit(item);
  }

  unselect(item: ListItemInterface) {
    if (!item.selected) return;

    switch (this.selection) {
      case 'single':
        if (!this.nullable) return;
        this.value = null;
        break;
      case 'multiple': {
        const currentValue = this.getCurrentValueAsArray();
        if (currentValue.length <= 1) return;
        if (currentValue.includes(item.id)) {
          this.value = currentValue.filter(id => id !== item.id);
        }
        break;
      }
      default:
        console.warn(`Unknown selection mode: ${this.selection}`);
    }
    this.selectChange.emit(item);
  }

  private getCurrentValueAsArray(): string[] {
    return Array.isArray(this.value) ? this.value : this.value ? [this.value] : [];
  }

  initDataSet() {
    if (!Array.isArray(this.dataSet)) return;
    this.dataSet.forEach(item => {
      item.id = item.id || generateUid('ilist-item');
    });
  }

  setSelectedItems() {
    if (!Array.isArray(this.dataSet)) return;

    const valueSet = new Set<string>(this.getCurrentValueAsArray());
    this.dataSet.forEach(item => {
      item.selected = valueSet.has(item.id);
    });
  }

  render() {
    const listGroup = this.renderListGroup();

    if (!this.headline) {
      return <Host>{listGroup}</Host>;
    }

    return (
      <Host>
        <storm-card scrollable={true} class="mt-4" bodyClass="p-0">
          <div slot="card-header">
            <div class="border-radius-xl mt-n4 mx-4 mb-2 bg-gradient-dark shadow-dark p-1">
              <h6 class="text-white font-weight-bolder text-center m-1">{this.headline}</h6>
            </div>
          </div>
          <div slot="body">{listGroup}</div>
        </storm-card>
      </Host>
    );
  }

  renderItems() {
    this._itemRefs = {};
    if (!Array.isArray(this.dataSet) || this.dataSet.length === 0) {
      return <div class="disabled list-group-item align-middle text-center p-4 h6">沒有找到符合的結果</div>;
    }

    const filteredItems = this.getFilteredItems();
    return filteredItems.map(item => this.renderItem(item));
  }

  private getFilteredItems(): ListItemInterface[] {
    let items = this.dataSet.filter(item => calculateObjectValue(this, item.visible) ?? true);

    if (this.searchable && this.searchText?.trim()) {
      const searchTerm = this.searchText.toLowerCase();
      items = items.filter(item => item.title?.toLowerCase().includes(searchTerm));
    }

    return items;
  }

  renderItem(item: ListItemInterface) {
    const disabled = (calculateObjectValue(this, item.disabled) ?? false) as boolean;
    return (
      <a
        href="javascript:void(0);"
        class={mergeClasses(
          {
            'list-group-item': true,
            'list-group-item-action': true,
            disabled: disabled,
            active: item.selected,
            'px-2': true,
            'py-1': true,
          },
          this.itemClass,
        )}
        onClick={ev => {
          ev.stopPropagation();
          this.itemClick.emit(item);
        }}
        ref={elm => (this._itemRefs[item.id] = elm)}
      >
        <FnIcon>{item.icon}</FnIcon>
        <FnIcon class={{ 'float-end': true }}>{item.selected && this.selection === 'multiple' ? 'check_circle' : ''}</FnIcon>
        {this.renderItemLabel(item.title)}
      </a>
    );
  }

  renderListGroup() {
    return (
      <div class={mergeClasses({ 'list-group': true, 'list-group-flush': !!this.headline }, this.class)}>
        {this.renderSearch()}
        {this.renderItems()}
      </div>
    );
  }

  renderItemLabel(value: string) {
    const shouldHighlight = this.searchText?.trim() && value;
    const displayValue = shouldHighlight ? appendHighlight(value, this.searchText) : value;
    return <span class={{ 'nav-link-text': true }} innerHTML={displayValue}></span>;
  }

  renderSearch() {
    if (!this.searchable) return;
    return (
      <div class="search">
        <FnSearchInput ref={elm => (this._searchRef = elm)} value={this.searchText} onValueChanged={ev => this.handleSearchChanged(ev)} />
      </div>
    );
  }

  componentWillLoad() {
    this.initDataSet();

    const hasValue = this.value && ((typeof this.value === 'string' && this.value.trim()) || (Array.isArray(this.value) && this.value.length > 0));

    if (!hasValue) {
      this.value = getSelectedItemIds(this.dataSet, this.nullable, this.selection);
    }

    this.setSelectedItems();
  }

  componentDidLoad() {
    const hasValidValue = (typeof this.value === 'string' && this.value.trim() !== '') || (Array.isArray(this.value) && this.value.length > 0);

    if (hasValidValue) {
      this.valueChanged.emit(this.value);
    }
  }
}
